"use client";
import Link from "next/link";
import { useAuth } from "../context/AuthContext";

export default function Navbar() {
  const { user, logout } = useAuth();

  return (
    <nav className="navbar navbar-expand-lg navbar-dark bg-dark px-3">
      <Link href="/" className="navbar-brand">ConstructionApp</Link>
      <div className="collapse navbar-collapse">
        <ul className="navbar-nav me-auto">
          <li className="nav-item">
            <Link href="/employees" className="nav-link">Employees</Link>
          </li>
          <li className="nav-item">
            <Link href="/projects" className="nav-link">Projects</Link>
          </li>
          <li className="nav-item">
            <Link href="/invoices" className="nav-link">Invoices</Link>
          </li>
        </ul>
        <div className="d-flex">
          {user ? (
            <>
              <span className="navbar-text me-2">Hi, {user.name}</span>
              <button className="btn btn-outline-light btn-sm" onClick={logout}>Logout</button>
            </>
          ) : (
            <Link href="/login" className="btn btn-outline-light btn-sm">Login</Link>
          )}
        </div>
      </div>
    </nav>
  );
}
